    .App {
        height: 100vh;
    }
    
    .nav {
        width: 100%;
        max-height: 128px;
        height: 20vw;
        display: flex;
        justify-content: space-between;
    }
    
    .logo {
        font-size: 80px;
        transition: ease-out 120ms;
    }
    
    .logo .small {
        font-size: 68px;
    }
    
    .logo .big {
        font-size: 100px;
        letter-spacing: 16px;
        font-weight: 600;
    }
    
    table {
        width: 100%;
    }
    
    table td {
        font-size: 24px;
    }
    
    table .table-button {
        width: 10vw;
        max-width: 64px;
    }
    
    input {
        width: 100%;
        background-color: gray;
        padding: 16px 0;
        border: none;
        border-radius: 4px;
    }
    
    .input-grid {
        margin: 16px 32px;
        display: grid;
        grid-template-columns: auto auto auto auto auto;
        grid-gap: 16px;
    }
    
    .input-grid-item {
        background-color: rgba(255, 255, 255, 0.2);
    }
    
    .input-two-grid {
        margin: 16px 32px;
        display: grid;
        grid-template-columns: auto 5% auto;
        grid-gap: 16px;
    }
    
    .big-description {
        font-size: 54px;
        font-weight: 600;
    }
    
    .tab {
        background-color: white;
        font-size: 30px;
        color: black;
        padding: 4px 32px;
        margin: 0 32px;
        border-radius: 4px;
    }
    
    .nav-button {
        cursor: pointer;
        max-width: 128px;
        max-height: 128px;
    }
    
    .current-set {
        font-size: 70vh;
        /* line-height: 70vh; */
    }
    
    .footer-links {
        bottom: 0;
        width: 100%;
        max-height: 128px;
        height: 20vw;
        display: flex;
        position: absolute;
        justify-content: space-around;
    }
    
    @media only screen and (max-width: 600px) {
        .logo {
            font-size: 60px;
        }
        .logo .small {
            font-size: 50px;
        }
        .logo .big {
            font-size: 68px;
            letter-spacing: 16px;
            font-weight: 600;
        }
    }